import { useState, useEffect } from "react";
import { makeStyles, Theme, createStyles } from "@material-ui/core/styles";
import CircularProgress from "@material-ui/core/CircularProgress";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";

import {
  convertFromWGS84ToTWD97,
  convertFromWGS84ToTWD67
} from "utils/coordinate";
import {
  COMMON_UNKNOWN,
  TITLE_WGS84,
  WGS84_LONGITUDE,
  WGS84_LATITUDE,
  WGS84_ACCURACY,
  WGS84_ALTITUDE,
  WGS84_ALTITUDE_ACCURACY,
  WGS84_UNIT,
  TITLE_TWD97,
  TWD97_X,
  TWD97_Y,
  TITLE_TWD67,
  TWD67_X,
  TWD67_Y
} from "utils/constants";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    loadingBox: {
      position: "absolute",
      top: "50%",
      left: "50%",
      transform: "translate(-50%, -50%)"
    },
    textLabel: {
      padding: theme.spacing(1)
    },
    textFieldGroup: {
      marginBottom: theme.spacing(4),
      "& .MuiTextField-root": {
        margin: theme.spacing(1),
        width: `calc(50% - ${theme.spacing(1) * 2}px)`
      }
    },
    containerCenter: {
      display: "flex",
      alignItems: "center"
    }
  })
);

export default () => {
  const classes = useStyles();
  const [isLoading, setIsLoading] = useState<Boolean>(true);
  const [longitude, setLongitude] = useState<null | number>(null);
  const [latitude, setLatitude] = useState<null | number>(null);
  const [altitude, setAltitude] = useState<null | number>(null);
  const [accuracy, setAccuracy] = useState<null | number>(null);
  const [altitudeAccuracy, setAltitudeAccuracy] = useState<null | number>(null);

  useEffect(() => {
    const geoOptions = {
      enableHighAccuracy: true
    };

    const watchId = navigator.geolocation.watchPosition(
      position => {
        const {
          longitude,
          latitude,
          accuracy,
          altitude,
          altitudeAccuracy
        } = position.coords;

        setLongitude(longitude);
        setLatitude(latitude);
        setAccuracy(accuracy);
        setAltitude(altitude);
        setAltitudeAccuracy(altitudeAccuracy);
        setIsLoading(false);
      },
      error => {
        // TODO: handle error
        console.log("Error occurred. Error code: " + error.code);
        // error.code can be:
        //   0: unknown error
        //   1: permission denied
        //   2: position unavailable (error response from location provider)
        //   3: timed out
      },
      geoOptions
    );

    return () => {
      navigator.geolocation.clearWatch(watchId);
    };
  }, []);

  if (!process.browser || isLoading) {
    return (
      <div className={classes.loadingBox}>
        <CircularProgress />
      </div>
    );
  }

  if (!navigator.geolocation) {
    return (
      <div>
        <p>Geolocation is not supported for your Browser/OS version yet.</p>
      </div>
    );
  }

  const twd97 =
    longitude && latitude
      ? convertFromWGS84ToTWD97([longitude, latitude])
      : null;
  const twd67 =
    longitude && latitude
      ? convertFromWGS84ToTWD67([longitude, latitude])
      : null;

  return (
    <Box m={1}>
      <div className={classes.textFieldGroup}>
        <div className={classes.containerCenter}>
          <Typography variant="h5" component="h3" className={classes.textLabel}>
            {TITLE_WGS84}
          </Typography>
        </div>
        <TextField
          label={WGS84_LONGITUDE}
          type="number"
          InputProps={{
            readOnly: true
          }}
          value={longitude || ""}
        />
        <TextField
          label={WGS84_LATITUDE}
          type="number"
          InputProps={{
            readOnly: true
          }}
          value={latitude || ""}
        />
        <TextField
          label={WGS84_ACCURACY}
          type="number"
          InputProps={{
            readOnly: true
          }}
          value={`${accuracy || COMMON_UNKNOWN} ${WGS84_UNIT}`}
        />
        <br />
        <TextField
          label={WGS84_ALTITUDE}
          type="number"
          InputProps={{
            readOnly: true
          }}
          value={`${altitude || COMMON_UNKNOWN} ${WGS84_UNIT}`}
        />
        <TextField
          label={WGS84_ALTITUDE_ACCURACY}
          type="number"
          InputProps={{
            readOnly: true
          }}
          value={`${altitudeAccuracy || COMMON_UNKNOWN} ${WGS84_UNIT}`}
        />
      </div>
      <div className={classes.textFieldGroup}>
        <div className={classes.containerCenter}>
          <Typography variant="h5" component="h3" className={classes.textLabel}>
            {TITLE_TWD97}
          </Typography>
        </div>
        <TextField
          label={TWD97_X}
          type="number"
          InputProps={{
            readOnly: true
          }}
          value={twd97?.[0] || ""}
        />
        <TextField
          label={TWD97_Y}
          type="number"
          InputProps={{
            readOnly: true
          }}
          value={twd97?.[1] || ""}
        />
      </div>
      <div className={classes.textFieldGroup}>
        <div className={classes.containerCenter}>
          <Typography variant="h5" component="h3" className={classes.textLabel}>
            {TITLE_TWD67}
          </Typography>
        </div>
        <TextField
          label={TWD67_X}
          type="number"
          InputProps={{
            readOnly: true
          }}
          value={twd67?.[0] || ""}
        />
        <TextField
          label={TWD67_Y}
          type="number"
          InputProps={{
            readOnly: true
          }}
          value={twd67?.[1] || ""}
        />
      </div>
    </Box>
  );
};
